export default {
	data() {
		return {
			scrollY: 0,
			currentIndex: -1,
			diff: -1,
			translateValue: 'translate3d(0, 0, 0)',
			titleHeight: 0,
			keyHeight: 0,
			topHeight: 0,
			touch: {},
			scrollInto: '',
			fixedTittle: '热门城市'
		};
	},
	props: {
		cityInfo: {
			type: Object,
			default: () => {}
		},
		keys: {
			type: Array,
			default: () => []
		}
	},
	computed: {
		idName() {
			return function(index) {
				index = Number(index) + 1;
				return `item-${index}`;
			};
		}
	},
	watch: {
		cityInfo() {
			this.$nextTick(function() {
				this._calculateHeight();
			});
		},
		scrollY(newY) {
			console.log(newY);
			const listHeight = this.listHeight;
			//  当滚动到顶部， newY <= 0
			if (newY == 0) {
				this.currentIndex = 0;
				this.fixedTittle = '热门城市'
				return;
			} else if (newY < 0) {
				this.currentIndex = -1;
				this.fixedTittle = ''
				return;
			}
			//  在中间部分滚动
			for (let i = 0; i < listHeight.length; i++) {
				let height1 = listHeight[i];
				let height2 = listHeight[i + 1];
				// console.log(i)
				// console.log(newY)
				// console.log(height1)
				// console.log(height2)
				if (!height2 || (newY >= height1 && newY < height2)) {
					this.currentIndex = i;
					console.log(i)
					if(i > 0){
						this.fixedTittle = this.keys[i - 1];
					} else{
						this.fixedTittle = '热门城市'
					}
					// console.log(this.currentIndex)
					// this.diff = height2 - newY;
					return;
				}
			}
			//  当滚动到底部，且newY大于最后一个元素的上限
			// this.currentIndex = listHeight.length - 1;
		},
		diff(newVal) {
			let that = this;
			let viewFixed = uni
				.createSelectorQuery()
				.in(this)
				.select('.list-fixed');
			viewFixed
				.boundingClientRect(data => {
					// console.log(data.top);
					that.titleHeight = data.height;
				})
				.exec();

			let fixedTop = newVal > 0 && newVal < that.titleHeight ? newVal - that.titleHeight : 0;
			if (this.fixedTop === fixedTop) {
				return;
			}
			this.fixedTop = fixedTop;
			// console.log(fixedTop)
			this.translateValue = `translate3d(0, ${fixedTop}px, 0)`;
		}
	},
	created() {
		this.listHeight = 0;
	},
	mounted() {},
	methods: {
		selectItem (name) {
			this.$emit('selectItem', name)
		},
		scroll(e) {
			// console.log(e.detail.scrollTop);
			this.scrollY = e.detail.scrollTop;
			// console.log(this.scrollY)
		},
		_calculateHeight() {
			this.listHeight = [];
			let that = this;

			uni
				.createSelectorQuery()
				.in(this)
				.select('.key')
				.boundingClientRect(data => {
					console.log(data);
					that.keyHeight = data.height;
				})
				.exec();

			let viewHot = uni
				.createSelectorQuery()
				.in(this)
				.select('.hot-wrap');
			viewHot
				.boundingClientRect(data => {
					that.topHeight = data.top;
					that.listHeight.push(data.top - that.topHeight);
				})
				.exec();

			let view = uni
				.createSelectorQuery()
				.in(this)
				.selectAll('.listGroup');
			view
				.boundingClientRect(data => {
					// console.log(data);
					data.forEach(function(item) {
						that.listHeight.push(item.top - that.topHeight);
					});
				})
				.exec();

			// console.log(this.listHeight)
		},
		onShortcutTouchSart(e) {
			console.log(e);
			// let anchorIndex = e.target.dataset.index;
			let firstTouch = e.touches[0];
			this.touch.y1 = firstTouch.pageY;
			let anchorIndex = Math.round((parseInt(this.touch.y1) - e.target.offsetTop) / this.keyHeight) ;  //获取触碰开始时是第几个
			console.log(anchorIndex);
			this.touch.anchorIndex = anchorIndex;
			this.scrollInto = `item-${anchorIndex}`;
			console.log(this.scrollInto);
		},
		onShortcutTouchMove(e) {
			console.log(e);
			let firstTouch = e.touches[0];
			this.touch.y2 = firstTouch.pageY;

			let delta = ((this.touch.y2 - this.touch.y1) / this.keyHeight) | 0;
			console.log('delta' + delta);
			console.log(this.touch.anchorIndex);
			let anchorIndex = parseInt(this.touch.anchorIndex) + delta;

			let str = '';
			if (anchorIndex == 0) {
				str = '热门城市';
			} else {
				str = this.keys[anchorIndex - 1];
			}
			uni.showToast({
				title: str,
				icon: 'none'
			});

			this.scrollInto = `item-${anchorIndex}`;
			console.log(this.scrollInto);
			// debugger;
		},
		leftTap(e) {
				let str = ''
				let index = e.currentTarget.dataset.index;
				this.currentIndex = Number(index);
				if (this.currentIndex == 0) {
					str  = '热门城市';
				} else {
					str = this.keys[this.currentIndex - 1];
				}
				uni.showToast({
				    title: str,
						icon: 'none'
				});
				// console.log(this.currentIndex);
				this.scrollInto = `item-${index}`;
		}
	}
};
